<template>
  <div class='container'>
     <van-nav-bar fixed title="搜索结果" left-arrow @click-left="$router.back()" />

      <van-list v-model="loading" @load="onLoad" :finished="finished" finished-text="没有更多了">
        <van-cell-group>
          <van-cell v-for="article in articleList" :key="article.art_id" :to="`/article?articleId=${article.art_id.toString()}`">
            <div class="article_item">
              <h3 class="van-ellipsis">{{article.title}}</h3>
              <div class="img_box" v-if="article.cover.type===3">
                <van-image class="w33" fit="cover" :src="article.cover.images[0]" />
                <van-image class="w33" fit="cover" :src="article.cover.images[1]" />
                <van-image class="w33" fit="cover" :src="article.cover.images[2]" />
              </div>
              <div class="img_box" v-if="article.type===1">
                <van-image class="w100" fit="cover" :src="article.cover.images[0]" />
              </div>
              <div class="info_box">
                <span>{{article.aut_name}}</span>
                <span>{{article.comm_count}}评论</span>
                <span>{{article.pubdate | relTime}}</span>
              </div>
            </div>
          </van-cell>
        </van-cell-group>
      </van-list>
  </div>
</template>

<script>
import { searchArtile } from '@/api/articles'
export default {
  data () {
    return {
      finished: false,
      articleList: [],
      loading: false,
      page: {
        page: 1,
        per_page: 10
      }
    }
  },
  methods: {
    async onLoad () {
      // 上拉刷新
      // 异步获取数据
      // 获得搜索的字
      let text = this.$route.query.q
      const { results } = await searchArtile({ q: text, ...this.page })
      this.articleList.push(...results)
      // console.log(this.articleList)
      // 手动关闭加载状态
      this.loading = false
      // 根据当前返回的数组长度 如果当前的长度为0表示下一页肯定没有数据了
      if (results.length) {
        // 有长度说明不为0
        this.page.page++
      } else {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  padding-top: 46px;
  height: 100%;
  overflow-y: auto;
  box-sizing: border-box;
}
.article_item {
  h3 {
    font-weight: normal;
    line-height: 2;
  }
  .img_box {
    display: flex;
    justify-content: space-between;
    .w33 {
      width: 33%;
      height: 90px;
    }
    .w100 {
      height: 180px;
      width: 100%;
    }
  }
  .info_box {
    color: #999;
    line-height: 2;
    position: relative;
    span {
      padding-right: 10px;
    }
  }
}
</style>
